body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

nav{
display:flex;
box-shadow: 0px 2.98256px 7.4564px rgba(0, 0, 0, 0.1);

}

.logo {
  padding : 30px;
  width:120px;
  height:40px;
}

.gridImg{
  margin-top : 30px;
  text-align: center;


}

.grid {
  width: 60%;
  height: 40%;

}

.quoteBox{
  text-align: center;
  font-size: 20px;
  padding : 80px;

}

.quote{
  font-family : Poppins;
  font-weight: 300px;
}

.gray{

  color:grey;
}

.star{
height:15px;

}

.cardStat{

  display: flex;
  align-items: center;

}

.cardImage{

  border-radius: 9px;

}

.cardBox{

  display:flex;
  justify-content: space-between;
  margin: 5%;
}